/* --- Tiles & Icons --- */

function MethodTile({
  title,
  desc,
  icon,
}: {
  title: string;
  desc: string;
  icon: React.ReactNode;
}) {
  return (
    <button
      type="button"
      className="w-full rounded-lg bg-[#F3F6FA] hover:bg-[#EBF2F9] transition-colors text-left px-4 py-4 flex items-center gap-4 border border-transparent"
    >
      <span className="shrink-0 text-[#2E6AA2]">{icon}</span>
      <span className="flex-1">
        <div className="text-[15px] font-semibold text-neutral-900">{title}</div>
        <div className="text-[14px] text-neutral-700">{desc}</div>
      </span>
      <span className="shrink-0 text-[#2E6AA2]">
        <ChevronRightIcon />
      </span>
    </button>
  );
}

function ChevronRightIcon() {
  return (
    <svg width="22" height="22" viewBox="0 0 24 24" fill="none" aria-hidden>
      <path d="M9 18l6-6-6-6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
    </svg>
  );
}

function EyeIcon({ open }: { open: boolean }) {
  return open ? (
    <svg width="22" height="22" viewBox="0 0 24 24" fill="none" aria-hidden>
      <path d="M2 12s3.5-7 10-7 10 7 10 7-3.5 7-10 7-10-7-10-7Z" stroke="currentColor" strokeWidth="2" />
      <circle cx="12" cy="12" r="3" stroke="currentColor" strokeWidth="2" />
    </svg>
  ) : (
    <svg width="22" height="22" viewBox="0 0 24 24" fill="none" aria-hidden>
      <path d="M2 12s3.5-7 10-7 10 7 10 7-3.5 7-10 7-10-7-10-7Z" stroke="currentColor" strokeWidth="2" />
      <path d="m3 21 18-18" stroke="currentColor" strokeWidth="2" />
    </svg>
  );
}

function PhoneIcon() {
  return (
    <svg width="24" height="24" viewBox="0 0 24 24" aria-hidden>
      <rect x="7" y="2" width="10" height="20" rx="2" ry="2" fill="none" stroke="currentColor" strokeWidth="2" />
      <circle cx="12" cy="18" r="1" fill="currentColor" />
    </svg>
  );
}

function GovIcon() {
  return (
    <svg width="24" height="24" viewBox="0 0 24 24" aria-hidden>
      <path d="M3 10h18M6 10V7a6 6 0 0 1 12 0v3" stroke="currentColor" strokeWidth="2" fill="none" />
      <rect x="4" y="10" width="16" height="10" rx="2" fill="none" stroke="currentColor" strokeWidth="2" />
    </svg>
  );
}

function CertIcon() {
  return (
    <svg width="24" height="24" viewBox="0 0 24 24" aria-hidden>
      <path d="M6 4h12v12H6z" fill="none" stroke="currentColor" strokeWidth="2" />
      <path d="M12 16v5l2-1 2 1v-5" fill="none" stroke="currentColor" strokeWidth="2" />
    </svg>
  );
}